<template>
  <div class="details_wrap">
    <div class="details_wrap_item">
      <div class="time">{{detailsList.orderNo}}<span :class="detailsList.statusClass">{{detailsList.statusPage}}</span></div>
      <div class="date">{{detailsList.createDatePage}}</div>
      <div class="content">
        <img :src="detailsList.category.icon" alt="" class="pic">
        <div>
          <div class="name">{{detailsList.category.name}}</div>
          <div class="price"><span>{{detailsList.price}}</span></div>
        </div>
      </div>
    </div>
    <div class="details_wrap_time">{{detailsList.arrivalTimePage}}<span class="btn_cancel" @click="openOrders" v-show="detailsList.status!=='COMPLETE'&&detailsList.status!=='CANCEL'"></span></div>
    <!-- div -->
    <!--  -->
    <div class="details_wrap_reason" v-show="detailsList.status=='CANCEL'||detailsList.status=='REJECTED'">
       <div class="why"></div>
       <div class="answer">{{detailsList.cancelReason}}</div>
    </div>
    <!--  -->
    <div class="details_wrap_reason" v-show="detailsList.status=='COMPLETE'||detailsList.status=='TOSEND'">
      <div class="why">{{detailsList.recyclerId}}<span class="btn_view" @click="openEvaluation" v-show="detailsList.status=='COMPLETE'">/</span></div>
      <div class="tel"><img src="@/assets/icon_tel.png" alt="" class="icon_tel">{{detailsList.tel}}</div>
    </div>
    <!--  -->
    <div class="details_wrap_reason" v-show="detailsList.status=='ALREADY'">
      <div class="why"></div>
      <div class="answer">1-3</div>
    </div>
    <div class="details_wrap_info">
      <div class="title"></div>
      <div class="picture">
        <img :src="pic.picUrl" alt="" v-for="pic in detailsPic"  :key="pic.type"  >
      </div>
      <div class="description">{{detailsList.cancelReason}}</div>
      <div class="lable">
        <span v-for="des in detailsDes"  :key="des.type"   >{{des.categoryAttrOpptionName}}</span>
      </div>
    </div>
    <div class="details_wrap_belongs">
      <div class="text"></div>
      <div class="text">400-8288-999</div>
    </div>
    <!--  --><div class="details_wrap_footbtn" @click="openCode" v-show="detailsList.status=='TOSEND'"></div>
    <!--  -->
    <div class="details_shadow" v-if="showShadow"></div>
    <div class="details_shadow_box" v-if="showOrders">
      <div class="title"></div>
      <div class="button_holder">
        <input type="radio" id="radio-1" name="radio" class="regular_radio"/><label for="radio-1"></label><br />
        <input type="radio" id="radio-2" name="radio" class="regular_radio" /><label for="radio-2"></label><br />
        <input type="radio" id="radio-3" name="radio" class="regular_radio" /><label for="radio-3"></label><br />
        <input type="radio" id="radio-4" name="radio" class="regular_radio" /><label for="radio-4"></label><br />
        <input type="radio" id="radio-5" name="radio" class="regular_radio" /><label for="radio-5"></label>
      </div>
      <div class="button_footer">
        <div class="btn_cancel" @click="closeOrders"></div>
        <div class="btn_confirm" @click="submitCancelOrders"></div>
      </div>
    </div>
    <!--  -->
    <div class="details_shadow_code" v-if="showCode">
      <img src="@/assets/icon_delete.png" alt="" class="icon_delete" @click="closeCode">
      <div class="code_text"></div>
      <img src="@/assets/code.png" alt="" class="pic_code">
    </div>
    <!--  -->
    <div class="details_shadow_evaluation" v-if="showEvaluation">
      <div class="title"></div>
      <div class="evaStar">
        <ul class="star">
        <li v-for="(itemClass,index) in itemClasses" :key="itemClass.type"  :class="itemClass" class="star-item" @click="stars(index)" track-by="index"></li>
        </ul>
      </div>
      <textarea name="" id="" class="text"></textarea>
      <div class="footer_btn" @click="closeEvaluation"></div>
    </div>
  </div>
</template>

<script>
  import '@/components/details/details.css'
  import api from '@/api/api.js'

  export default {
    data(){
      return {
        showShadow: false,
        showOrders: false,
        showEvaluation: false,
        showCode: false,
        score: 4,
        detailsList: {},
        detailsPic: {},
        detailsDes: {},
        id: this.$route.query.id,
      }
    },
    mounted() {
      //
      api.getDetails({
        "app_key": "app_id_1",
        "data": {
          "id": this.id,
          "isEvaluated": "0",
          "status": 0
        },
      }).then((res) => {
        console.log(res.data);
        var status = res.data.order.statusPage;
        switch (status) {
          case '':
            res.data.order.statusClass = 'succeed';
            break;
          case '':
            res.data.order.statusClass = 'complete';
            break;
          case '':
            res.data.order.statusClass = 'waiting';
            break;
          case '':
            res.data.order.statusClass = 'cancel';
            break;
          case '':
            res.data.order.statusClass = 'cancel';
            break;
          case '':
            res.data.order.statusClass = 'succeed';
            break;
          default:
            break;
        }
        this.detailsList = res.data.order;
        this.detailsPic = res.data.orderPicList;
        this.detailsDes = res.data.OrderItemList;
      }).catch((erro) => {
        console.log(erro)
      })
    },
    computed:{ //
      itemClasses(){
        var result = []; // ,
        var score = Math.floor(this.score * 2 ) / 2; // 
        var hasDecimal = score % 1 !== 0; // 
        var integer = Math.floor(score); // 
        for(var i=0;i<integer;i++){ // on
          result.push("on"); // pushCLS_ON
        }
        if(hasDecimal){ // half
          result.push("half"); // 
        } 
        while(result.length < 5){// ,off
          result.push("off");
        }
        return result;
      }
    },
    methods:{
      openOrders(){
        this.showShadow = true;
        this.showOrders = true;
      },
      closeOrders(){
        this.showShadow = false;
        this.showOrders = false;
      },
      openEvaluation(){
        this.showShadow = true;
        this.showEvaluation = true;
      },
      closeEvaluation(){
        this.showShadow = false;
        this.showEvaluation = false;
      },
      openCode(){
        this.showShadow = true;
        this.showCode = true;
      },
      closeCode(){
        this.showShadow = false;
        this.showCode = false;
      },
      stars(index){
        this.score = index + 1
      },
      submitCancelOrders(){
        api.cancelOrders({
          "app_key": "app_id_1",
          "data": {
            "id": this.id,
            "cancelReason": "",
            "isEvaluated": "0"
          },
        }).then((res) => {
          console.log(res.data);
          this.showShadow = false;
          this.showOrders = false;
        }).catch((erro) => {
          console.log(erro)
        })
      },
    }
  }
</script>



